* {
  box-sizing: border-box;
}

a {
  color: white;
  text-decoration: none; /* no underline */
}

menu {
  margin: 0;
  padding: 0;
}

button,
input[type='submit'],
input[type='reset'] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

ul {
  list-style: none;
}

ul,
li {
  /* reset the margin and padding */
  margin: 0;
  padding: 0;
}

*::-webkit-scrollbar {
  width: 8px; /* в основном для вертикальных полос прокрутки */
  height: 8px; /* в основном для горизонтальных полос прокрутки */
}
*::-webkit-scrollbar-thumb {
  /* плашка-бегунок */
  background: #8585ad; /* фон */
}
*::-webkit-scrollbar-track {
  /* фон */
  width: 0 0 0 8px;
  background: #2f2f37;
}
