.content {
  padding: 80px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}

.identifier {
  text-shadow:
    0px 4px 32px rgba(51, 51, 255, 0.5),
    0px 0px 8px rgba(51, 51, 255, 0.25),
    0px 0px 16px rgba(51, 51, 255, 0.25);
}

.doneLabel {
  position: relative;
}

.doneIcon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.status {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}
